<script>
  import {Select, Option, Button, Input, DatePicker, Radio, Message} from 'element-ui'
  import Editor from '@tinymce/tinymce-vue'
  import MySelect from 'src/components/UIComponents/Select'
  import MyMoney from 'src/components/UIComponents/Money'
  import SelectAjax from 'src/components/UIComponents/SelectAjax'

  export default {
    components: {
      ElSelect: Select,
      ElOption: Option,
      ElButton: Button,
      ElInput: Input,
      ElDatePicker: DatePicker,
      ElRadio: Radio,
      editor: Editor,
      MySelect,
      MyMoney,
      SelectAjax
    },
    created() {
      let self = this;
      this.$validator.extend('earlier', {
        getMessage(field, val) {
          return 'Thời gian kết thúc phải lớn hơn thời gian bắt đầu'
        },
        validate(value, field) {
          let startParts = self.form.start_date
          let momentStart = new Date(startParts).getTime();
          let momentEnd = new Date(value).getTime();
          return momentEnd > momentStart;
        }
      })
    },
    data () {
      return {
        form: {
          type: '',
          status: '',
          value: 0,
          type_relation: 'product',
          products: []
        },
        status: {
          options: [
            {
              title: "Hiển thị",
              value: "active"
            }, {
              title: "Ẩn",
              value: "inactive"
            }
          ]
        },
        typesProduct: {
          options: [
            {
              title: "Sản phẩm",
              value: "product"
            }, {
              title: "Nhóm sản phẩm",
              value: "collection"
            }
          ]
        },
        types: {
          options: [
            {
              title: "Giảm theo đơn giá",
              value: "value"
            }, {
              title: "Giảm theo phần trăm",
              value: "percent"
            }
          ]
        }
      }
    },

    mounted() {
      this.$store.dispatch('setPageTitle', 'Tạo chương trình khuyến mãi');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Lưu lại',
        type: 'primary',
        icon: '',
        callback: this.save
      }])

      this.form.status = 'active';
      this.form.type = 'value';
    },

    methods: {
      cancel() {
        this.$router.push({name: 'AllSales'});
      },
      save() {
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            let products = self.form.products;
            let data = self.form;
            data.products = products.map(item => {
              return item.id;
            })
            self.$store.dispatch('storeSale', data).then(res => {
              Message({
                message: 'Tạo mới thành công',
                type: 'success'
              });
              self.$router.push({name: 'AllSales'})
            }, res => {
              Message({
                message: res.message,
                type: 'danger'
              });
            });
          }
        })
      }
    },
    watch: {

    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
